import * as React from "react";
import List from "@mui/material/List/List";
import ListItem from "@mui/material/ListItem/ListItem";
import ListItemButton from "@mui/material/ListItemButton/ListItemButton";
import ListItemIcon from "@mui/material/ListItemIcon/ListItemIcon";
import ListItemText from "@mui/material/ListItemText/ListItemText";
import Checkbox from "@mui/material/Checkbox/Checkbox";

import useHttp from "../../hooks/use-http";
import Card from "../UI/Card/Card";

import NewDate from "./NewDate";

export default function Checklist() {
	const [checked, setChecked] = React.useState([]);

	const [dates, setDates] = React.useState([]);

	const { sendRequest } = useHttp();

	React.useEffect(() => {
		const transformDates = (datesObj) => {
			const loadedDates = [];

			for (const dateKey in datesObj) {
				loadedDates.push({
					id: dateKey,
					text: datesObj[dateKey].text,
					checked: datesObj[dateKey].checked,
				});
			}

			setDates(loadedDates);
            console.log("IN")
            console.log(loadedDates.filter(x => x.checked === true));
			setChecked(loadedDates.filter(x => x.checked === true).reverse());
		};

		sendRequest(
			{
				url: "https://bondi-8a94c-default-rtdb.firebaseio.com/dates.json",
			},
			transformDates
		);
	}, [sendRequest]);

	const dateAddHandler = (date) => {
        window.location.reload(false);
	};

	const createDate = (dateText, dateData) => {
		const generatedId = dateData.name; // firebase-specific => "name" contains generated id
		const createdDate = { id: generatedId, text: dateText };

		dateAddHandler(createdDate);
	};

	const enterDateHandler = async (dateText) => {
		sendRequest(
			{
				url: "https://bondi-8a94c-default-rtdb.firebaseio.com/dates.json",
				method: "POST",
				headers: {
					"Content-Type": "application/json",
				},
				body: { text: dateText, checked: false },
			},
			createDate.bind(null, dateText)
		);
	};

	const handleToggle = (value) => () => {
		const item = checked.find((x) => x.id === value.id);
		const newChecked = [...checked];

        console.log(item);

		if (!item) {
			newChecked.push(value);
			const datesIndex = dates.indexOf(value);
			setDates((oldDates) =>
				oldDates.map((x, i) =>
					i === datesIndex ? { ...x, checked: true } : x
				)
			);
			sendRequest({
				url:
					"https://bondi-8a94c-default-rtdb.firebaseio.com/dates/" +
					value.id +
					".json",
				method: "PUT",
				headers: {
					"Content-Type": "application/json",
				},
				body: { text: value.text, checked: true },
			});
		} else {
			const currentIndex = checked.indexOf(item);
			newChecked.splice(currentIndex, 1);
			const datesIndex = dates.indexOf(value);
			setDates((oldDates) =>
				oldDates.map((x, i) =>
					i === datesIndex ? { ...x, checked: false } : x
				)
			);
			sendRequest({
				url:
					"https://bondi-8a94c-default-rtdb.firebaseio.com/dates/" +
					value.id +
					".json",
				method: "PUT",
				headers: {
					"Content-Type": "application/json",
				},
				body: { text: value.text, checked: false },
			});
		}

		setChecked(newChecked);
	};

	return (
		<>
			<h4
				style={{
					fontSize: "25px",
					marginBlock: "15px",
					textAlign: "center",
                    padding: "1rem",
                    margin: "2rem auto",
                    width: "90%",
                    maxWidth: "40rem",
				}}
			>
				Date Checklist
			</h4>
			<Card>
				<List
					sx={{
						width: "100%",
						maxWidth: 360,
						bgcolor: "background.paper",
					}}
				>
					{dates.map((value) => {
						const labelId = `checkbox-list-label-${value}`;

						return (
							<ListItem key={value.id}>
								<ListItemButton
									role={undefined}
									onClick={handleToggle(value)}
									dense
								>
									<ListItemIcon>
										<Checkbox
											style={{
												color: "purple",
											}}
											edge="start"
											checked={value.checked}
											tabIndex={-1}
											inputProps={{
												"aria-labelledby": labelId,
											}}
										/>
									</ListItemIcon>
									<ListItemText
										id={labelId}
										primary={value.text}
										primaryTypographyProps={{
											fontSize: "20px",
										}}
									/>
								</ListItemButton>
							</ListItem>
						);
					})}
				</List>
			</Card>
            
            <NewDate onAddDate={enterDateHandler} />
		</>
	);
}
